@import url('https://fonts.googleapis.com/css2?family=Amatic+SC:wght@700&family=Roboto:ital,wght@1,500&family=Rubik+Microbe&display=swap');

.carouselTile {
    background-color: aquamarine;
    width: 100%;
    display: flex;
    flex-direction: row;
    justify-content: center;
    align-items: center;
    gap: 5%;
}

.carouselTile img{
    height: 40%;
    width: 40%;
}

.carouselTile p{
    margin-right: 5%;
}

.container {
    padding-top: 8%;
    display: flex;
    flex-direction: row;
    justify-content: space-around;
}

.jobTile {
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;
}

.aboutMe {
    padding-top: 5%;
    display: flex;
    flex-direction: row;
    justify-content: center;
    align-items: center;
    gap: 2rem;
}

.work-section {
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;
}

.work-tile {
    display: flex;
    flex-direction: column;
    justify-content: center;
}

.workP {
    display: flex;
    flex-direction: column;
    align-items: center;
    justify-content: center;
    width: 80%;
    text-align: center;
}

.paragraph {
    width: 50%;
    padding: 3rem;
    font-family: "Amatic SC";
    font-size: xx-large;
}

.aboutButtons {
    padding-top: 5rem;
    display: flex;
    flex-direction: row;
    justify-content: space-around;
}

.home-hero {
    padding-top: 5%;
    display: flex;
    flex-direction: column;
    justify-content: space-around;
    align-items: center;
}

.navButton {
    background-color: lightgray;
    margin-top: 1rem;
    border-radius: 5%;
    border: none;
    width: 6rem;
    height: 3rem;
    display: flex;
    justify-content: center;
    align-items: center;
    text-decoration: none;
    
}

.navButtons {
    display: flex;
    flex-direction: row;
    gap: 2%;
}

.contact-buttons {
    display: flex;
    justify-content: space-around;
    height: 3rem;
}

.contact-button {
    height: 3rem;
    background-color: aquamarine;
}

.project-card {
    display: flex;
    flex-direction: column;
    justify-content: center;
}

.pic-small {
    height: 30%;
    width: 30%;
    border-radius: 50%;
    border: 3px solid #555;
}

.pic-hero{
    height: 25%;
    width: 25%;
    margin-left: 5%;
    border-radius: 20%;
}

.site-title {
    font-size: 2rem;
}

nav {
    background-color: grey;
    color: white;
    display: flex;
    justify-content: space-between;
    align-items: stretch;
    gap: 2rem;
    position: fixed;
    width: 100%;
    z-index: 10;
}

nav ul {
    padding-right: 5%;
    list-style: none;
    display: flex;
    gap: 2rem;
}

nav a {
    text-decoration: none;
    color: inherit;
    height: 100%;
    display: flex;
    align-items: center;
    padding: .25rem;
}

nav li:active {
    background-color: blue;
    
}

nav li:hover {
    background-color: brown;
}


nav .menu {
    display: none;
    position: absolute;
    top: .75rem;
    right: .5rem;
    flex-direction: column;
    justify-content: space-between;
    width: 2.25rem;
    height: 2rem;
}

nav .menu span {
    height: .4rem;
    width: 100%;
    background-color: white;
    border-radius: .2rem;
}

@media (max-width: 610px) {
    nav .menu {
        display: flex;
    }

    nav {
        flex-direction: column;
        align-items: flex-start;
        gap: 0;
    }

    nav ul {
        display: none;
        width: 100%;
        margin-bottom: .25rem;
        gap: 0;
    }

    nav ul.open {
        display: flex;
        background-color: lightgray;
    }

    nav ul li {
        width: 100%;
        text-align: center;
    }

    nav ul li a {
        margin: .2rem .5rem;
    }
}

@media (max-width: 350px) {
    .container {
        padding-top: 20%;
        flex-direction: column;
        justify-content: center;
        text-align: center;
    }
}
